.select {
  position: relative;

  .options {
    width: 100%;
  }

  select {
    display: block;
    appearance: none;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: 12px;
    opacity: 0;
    z-index: -1;
  }
}



