@import url(~normalize.css/normalize.css);
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: #000;
  background-color: #f5f5f5;
  font-family: "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
}

p {
  margin-top: 24px;
  margin-bottom: 24px;
  line-height: 1.5;
}

small {
  font-size: 12px;
  line-height: 24px;
}

h1,
.h1, h2,
.h2, h3,
.h3, h4,
.h4, h5,
.h5, h6,
.h6 {
  font-weight: 700;
  margin-top: 24px;
}

h1,
.h1, h2,
.h2 {
  margin-bottom: 24px;
}

h3,
.h3, h4,
.h4, h5,
.h5, h6,
.h6 {
  margin-bottom: 0;
}

h1,
.h1 {
  font-size: 40px;
  line-height: 48px;
}

h2,
.h2 {
  font-size: 30px;
  line-height: 48px;
}

h3,
.h3 {
  font-size: 22px;
  line-height: 24px;
}

h4,
.h4 {
  font-size: 16px;
  line-height: 24px;
}

h5,
.h5 {
  font-size: 16px;
  line-height: 24px;
}

h6,
.h6 {
  font-size: 16px;
  line-height: 24px;
}

a {
  color: #3a99d8;
  text-decoration: none;
  transition: color 0.15s ease-in-out;
}

a:hover {
  color: #65afe0;
  text-decoration: none;
}

.container {
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 12px;
  padding-right: 12px;
}

.container:before, .container:after {
  content: "";
  display: table;
}

.container:after {
  clear: both;
}

.row {
  margin-left: -12px;
  margin-right: -12px;
}

.row:before, .row:after {
  content: "";
  display: table;
}

.row:after {
  clear: both;
}

.cols-1 {
  width: 8.33333%;
}

.cols-2 {
  width: 16.66667%;
}

.cols-3 {
  width: 25%;
}

.cols-4 {
  width: 33.33333%;
}

.cols-5 {
  width: 41.66667%;
}

.cols-6 {
  width: 50%;
}

.cols-7 {
  width: 58.33333%;
}

.cols-8 {
  width: 66.66667%;
}

.cols-9 {
  width: 75%;
}

.cols-10 {
  width: 83.33333%;
}

.cols-11 {
  width: 91.66667%;
}

.cols-12 {
  width: 100%;
}

[class*=cols] {
  float: left;
  min-height: 1px;
  padding-left: 12px;
  padding-right: 12px;
}

.split {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.split .left,
.split .center,
.split .right {
  display: table-cell;
  vertical-align: top;
}

.split .left {
  text-align: left;
}

.split .center {
  text-align: center;
}

.split .right {
  text-align: right;
}



